import React from   'react';
var     count = 0;
class VariableCounter extends React.Component {
    constructor(props){
        super(props);
        this.state={intcount:0}
    }
    increment = () => {
        this.setState(p =>({intcount : p.intcount +1}))
        alert(this.state.intcount);
      };
    render() {
        
      return (
        <div>
          <p>The count is: {this.state.intcount}</p>
          <button onClick={this.increment}>Add one</button>
        </div>
      );
    }

  } 
export default VariableCounter;


